* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul {
  margin-right: 150px;
}

.navbar {
  background-color: gray;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
}

.nav-list {
  list-style-type: none;
}

.nav-list {
  li {
    display: inline-block;
    padding: 10px 10px;
    border: 1px solid gray;

    margin: 0 50px;
  }
}

.nav-list {
  li:hover {
    background-color: black;
    cursor: pointer;
    transition: 0.7s;
    border-radius: 5px;
  }
}

.logo {
  height: 60px;
  margin-left: 40px;
}

.logo:hover {
  cursor: pointer;
}
